@import "../../_globalColor";

.main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 4rem;
}
.greet-main {
  width: 90%;
  padding: 20px 10px;
  margin: 0px auto;
  margin-top: 2rem;
}

.greeting-main {
  display: flex;
}

.subTitle {
  color: $subTitle !important;
}

.greeting-main {
  display: flex;
}

.greeting-main > * {
  flex: 1;
  margin-bottom: 30px;
}

.empty-div {
  margin-bottom: 40px;
}

.button-greeting-div {
  display: flex;
  margin-top: 40px;
}
.download-link-button {
  text-decoration: none;
}

.greeting-text {
  font-size: 70px;
  line-height: 1.1;
  color: $textColor !important;
}

.greeting-text-p {
  font-size: 30px;
  line-height: 40px;
}

.greeting-image-div > img {
  max-width: 100%;
  height: auto;
}

.wave-emoji {
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-name: wave;
  display: inline-block;
  transform-origin: 70% 70%;
}

@keyframes wave {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

/* Media Query */
@media (max-width: 1380px) {
  .greeting-text {
    font-size: 50px;
  }
  .greeting-text-p {
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  .greet-main {
    padding: 2rem 1rem;
  }
  
  .button-greeting-div {
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }
  
  .greeting-text {
    font-size: 30px;
    text-align: center;
    line-height: 1.2;
  }
  
  .greeting-text-p {
    font-size: 16px;
    line-height: 1.4;
    text-align: center;
    margin: 20px 0;
  }
  
  .greeting-main {
    display: block;
  }
  
  .greeting-image-div {
    height: auto;
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .greeting-image-div > img {
    max-width: 70%;
    height: auto;
  }
  
  .main-button {
    width: 100%;
    max-width: 250px;
    margin: 5px 0;
  }
}

@media (max-width: 480px) {
  .greet-main {
    padding: 1rem 0.5rem;
    margin-top: 1rem;
  }
  
  .greeting-text {
    font-size: 24px !important;
  }
  
  .greeting-text-p {
    font-size: 14px !important;
  }
  
  .greeting-image-div > img {
    max-width: 80%;
  }
}

@media (max-width: 320px) {
  .main {
    width: auto;
  }
}
